"use client"

import { useEffect, useState } from "react"
import { createClient } from "@/lib/supabase/client"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Progress } from "@/components/ui/progress"
import { Loader2, CheckCircle2, Circle, Zap } from "lucide-react"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import {
  ensureTopicProgress,
  handleVideoComplete,
  handleTestSubmit,
  handleRevisionComplete,
  openMaterial,
  type TopicProgressData,
} from "@/lib/questLog/questLogSystem"

interface Topic {
  id: string
  name: string
  description: string
}

interface QuestData {
  topic: Topic
  progress: TopicProgressData | null
}

interface QuestLogProps {
  studentId: string
}

export function QuestLog({ studentId }: QuestLogProps) {
  const [quests, setQuests] = useState<QuestData[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [testScores, setTestScores] = useState<Record<string, number>>({})
  const [submittingTest, setSubmittingTest] = useState<Record<string, boolean>>({})
  const [completingRevision, setCompletingRevision] = useState<Record<string, boolean>>({})
  const supabase = createClient()

  useEffect(() => {
    fetchQuests()
  }, [studentId])

  const fetchQuests = async () => {
    try {
      setLoading(true)
      setError(null)

      // Fetch all topics
      const { data: topics, error: topicsError } = await supabase
        .from("topics")
        .select("id, name, description")
        .order("created_at", { ascending: true })

      if (topicsError) throw new Error(topicsError.message)

      // Fetch all topic progress for this student
      const { data: progressData } = await supabase
        .from("topic_progress")
        .select("*")
        .eq("student_id", studentId)

      const progressMap = new Map((progressData || []).map((p) => [p.topic_id, p]))

      // Build quest list
      const questData: QuestData[] = (topics || []).map((topic) => ({
        topic,
        progress: progressMap.get(topic.id) || null,
      }))

      setQuests(questData)
    } catch (err) {
      const msg = err instanceof Error ? err.message : "Failed to load quests"
      setError(msg)
      console.error("Error fetching quests:", err)
    } finally {
      setLoading(false)
    }
  }

  const handleVideoClick = async (topicId: string) => {
    try {
      // Ensure progress exists and mark video complete
      const updated = await handleVideoComplete(studentId, topicId)
      if (updated) {
        // Update local state
        setQuests((prev) =>
          prev.map((q) =>
            q.topic.id === topicId ? { ...q, progress: updated } : q
          )
        )

        // Open material in new tab
        await openMaterial(studentId, topicId, "video")
      }
    } catch (err) {
      console.error("Error handling video:", err)
    }
  }

  const handleTestScoreChange = (topicId: string, value: string) => {
    const score = Math.min(Math.max(parseInt(value) || 0, 0), 20)
    setTestScores((prev) => ({
      ...prev,
      [topicId]: score,
    }))
  }

  const handleTestSave = async (topicId: string) => {
    const score = testScores[topicId] || 0

    try {
      setSubmittingTest((prev) => ({ ...prev, [topicId]: true }))

      const updated = await handleTestSubmit(studentId, topicId, score)
      if (updated) {
        setQuests((prev) =>
          prev.map((q) =>
            q.topic.id === topicId ? { ...q, progress: updated } : q
          )
        )

        // Open test material
        await openMaterial(studentId, topicId, "test")
      }
    } catch (err) {
      console.error("Error saving test score:", err)
    } finally {
      setSubmittingTest((prev) => ({ ...prev, [topicId]: false }))
    }
  }

  const handleRevisionClick = async (topicId: string, revisionIndex: number) => {
    const key = `${topicId}-${revisionIndex}`

    try {
      setCompletingRevision((prev) => ({ ...prev, [key]: true }))

      const updated = await handleRevisionComplete(studentId, topicId, revisionIndex)
      if (updated) {
        setQuests((prev) =>
          prev.map((q) =>
            q.topic.id === topicId ? { ...q, progress: updated } : q
          )
        )

        // Open appropriate material
        const materialTypes = ["pdf", "video", "test"] as const
        const materialType = materialTypes[revisionIndex - 1] || "pdf"
        await openMaterial(studentId, topicId, materialType)
      }
    } catch (err) {
      console.error("Error completing revision:", err)
    } finally {
      setCompletingRevision((prev) => ({ ...prev, [key]: false }))
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case "MASTERED":
        return "bg-green-100 text-green-800"
      case "GOOD":
        return "bg-yellow-100 text-yellow-800"
      default:
        return "bg-red-100 text-red-800"
    }
  }

  const completedQuests = quests.filter(
    (q) => q.progress && q.progress.progress_percentage === 100
  ).length

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <Loader2 className="w-8 h-8 animate-spin" />
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-3xl font-bold">ðŸ“œ Quest Log</h1>
        <p className="text-muted-foreground mt-2">
          {completedQuests}/{quests.length} Quests Complete
        </p>
      </div>

      {error && (
        <Card className="border-destructive">
          <CardContent className="p-4">
            <p className="text-destructive">Error: {error}</p>
          </CardContent>
        </Card>
      )}

      {/* Quests Table */}
      <Card>
        <CardHeader>
          <CardTitle>Active Quests</CardTitle>
          <CardDescription>Track your progress on each topic</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead className="w-32">Quest Name</TableHead>
                  <TableHead className="text-center w-20">Video</TableHead>
                  <TableHead className="text-center w-24">Test Score</TableHead>
                  <TableHead className="text-center w-16">Rev 1</TableHead>
                  <TableHead className="text-center w-16">Rev 2</TableHead>
                  <TableHead className="text-center w-16">Rev 3</TableHead>
                  <TableHead className="text-center w-16">Rev 4</TableHead>
                  <TableHead className="text-center w-16">XP</TableHead>
                  <TableHead className="text-center w-24">Status</TableHead>
                  <TableHead className="w-32">Progress</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {quests.map((quest) => {
                  const p = quest.progress
                  const hasStarted = p !== null

                  return (
                    <TableRow key={quest.topic.id}>
                      {/* Quest Name */}
                      <TableCell className="font-medium text-sm">
                        <div>
                          <p className="font-semibold">{quest.topic.name}</p>
                          <p className="text-xs text-muted-foreground">{quest.topic.description}</p>
                        </div>
                      </TableCell>

                      {/* Video */}
                      <TableCell className="text-center">
                        {p?.video_completed ? (
                          <CheckCircle2 className="w-5 h-5 text-green-600 mx-auto" />
                        ) : (
                          <Button
                            size="sm"
                            variant="outline"
                            className="w-full"
                            onClick={() => handleVideoClick(quest.topic.id)}
                          >
                            Watch
                          </Button>
                        )}
                      </TableCell>

                      {/* Test Score */}
                      <TableCell className="text-center">
                        {p?.test_completed ? (
                          <div className="text-sm font-semibold text-green-700">
                            {p.test_score}/20
                          </div>
                        ) : (
                          <div className="flex gap-1 items-center justify-center">
                            <Input
                              type="number"
                              min="0"
                              max="20"
                              placeholder="0"
                              value={testScores[quest.topic.id] ?? ""}
                              onChange={(e) => handleTestScoreChange(quest.topic.id, e.target.value)}
                              className="w-12 h-8 text-center text-xs"
                            />
                            <Button
                              size="sm"
                              className="h-8"
                              onClick={() => handleTestSave(quest.topic.id)}
                              disabled={submittingTest[quest.topic.id]}
                            >
                              {submittingTest[quest.topic.id] ? (
                                <Loader2 className="w-3 h-3 animate-spin" />
                              ) : (
                                "Save"
                              )}
                            </Button>
                          </div>
                        )}
                      </TableCell>

                      {/* Revisions */}
                      {[1, 2, 3, 4].map((rev) => {
                        const isCompleted = p?.[`revision_${rev}_completed` as keyof TopicProgressData]
                        const key = `${quest.topic.id}-${rev}`

                        return (
                          <TableCell key={rev} className="text-center">
                            {isCompleted ? (
                              <CheckCircle2 className="w-5 h-5 text-green-600 mx-auto" />
                            ) : (
                              <Button
                                size="sm"
                                variant="outline"
                                className="w-full h-8"
                                onClick={() => handleRevisionClick(quest.topic.id, rev)}
                                disabled={completingRevision[key]}
                              >
                                {completingRevision[key] ? (
                                  <Loader2 className="w-3 h-3 animate-spin" />
                                ) : (
                                  <Circle className="w-3 h-3" />
                                )}
                              </Button>
                            )}
                          </TableCell>
                        )
                      })}

                      {/* XP */}
                      <TableCell className="text-center font-bold">
                        {p?.total_xp || 0}
                      </TableCell>

                      {/* Status */}
                      <TableCell className="text-center">
                        <Badge className={getStatusColor(p?.status || "NEEDS_WORK")}>
                          {p?.status || "Not Started"}
                        </Badge>
                      </TableCell>

                      {/* Progress */}
                      <TableCell>
                        <div className="space-y-1">
                          <Progress
                            value={p?.progress_percentage || 0}
                            className="h-2"
                          />
                          <p className="text-xs text-muted-foreground text-center">
                            {p?.progress_percentage || 0}%
                          </p>
                        </div>
                      </TableCell>
                    </TableRow>
                  )
                })}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
